import { useState, useEffect, useCallback, useMemo } from "react"

interface queryArgs {
    query: string
    variables?: {[key: string]: string}
    token?: string
    passive?: boolean 
}

interface returnValue<T> {
    data: T
    loaded: boolean, 
    fetch?: () => Promise<void>
}

export function useGraphql<dataType>(args: queryArgs): returnValue<dataType>

export default function useGraphql<dataType>({ query, variables: vars, token, passive = false }: queryArgs): returnValue<dataType> {
    const [data, set] = useState<dataType>(null)

    const variables = useVariables(vars)

    //const variables = updateObj(vars)

    const Authorization = useMemo(() => token && {
        Authorization: `bearer ${token}`
    }, [token])

    const fetchData = useCallback(async() => {
        const init: RequestInit = {
            method: "POST",
            headers: {
                ...Authorization,
                "Content-Type": "application/json",
                "Accept": "application/json"
            },
            body: JSON.stringify({
                query, variables
            })
        }

        try {
            const response: Response = await fetch('/graphql', init)

            const { data: jsonResponse }: { data: dataType } = await response.json()

            set((prev: dataType) => {
                if (!prev) {
                    return jsonResponse
                } else {
                    if (JSON.stringify(prev) !== JSON.stringify(jsonResponse)) {
                        return jsonResponse
                    } else {
                        return prev
                    }    
                }
            })
        } catch (error) {
            console.log(error)

            set(null)
        }
    }, [query, variables, Authorization])
    
    useEffect(() => {
        let mounted = true

        if (mounted) {
            if (!passive) {
                fetchData()
            }
        }

        return () => {
            mounted = false

            set(null)
        }
    }, [fetchData, passive])
    
    if (data) {
        for (const [, value] of Object.entries(data)) {
            return {
                data: value, 
                loaded: true,
                fetch: fetchData
            }
        }
    }

    return {
        data: null, 
        loaded: false,
        fetch: fetchData
    }
}

function useVariables(variables: object): string

function useVariables(variables: object): string {
    const current = JSON.stringify(variables)

    const [result, set] = useState<string>(current)

    useEffect(() => {
        set(prev => {
            if (prev !== current) {
                return current
            }

            return prev
        })

        return () => {
            set(current)
        }
    }, [current])

    return result
}