import React from "react"
import Input from "@components/input"
import Button from "@components/button"
import useGraphql from "@hooks/use-graphql"
import './up.css'

export default function SignUp() {
    const [input, setInput] = React.useState({})

    const { data, loaded, fetch } = useGraphql({
        query: `mutation ($email: String!, $password: String!, $username: String!, $firstname: String, $lastname: String, $phone: String, $birthdate: Float, $bio: String) {
            addUser(email: $email, password: $password, username: $username, info: {firstname: $firstname, lastname: $lastname, phone: $phone, birthdate: $birthdate, bio: $bio}) {
              email
              username
            }
          }`,
        variables: {...input},
        passive: true
    })

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInput(input => ({
            ...input,
            [e.target.name]: e.target.value
        }))
    } 

    const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()

        fetch()
    }

    React.useEffect(() => {
        document.title = 'Sign Up'

        if (loaded) alert(JSON.stringify(data))
    }, [loaded, data])

    return(
        <form {...{onSubmit}}>
            <Input type="email" name="email" {...{onChange}}>
                Email
            </Input>
            <Input type="password" name="password" {...{onChange}}>
                Password
            </Input>
            <Input type="text" name="username" {...{onChange}}>
                Username
            </Input>
            <Input type="text" name="firstname" {...{onChange}}>
                First Name
            </Input>
            <Input type="text" name="lastname" {...{onChange}}>
                Last Name
            </Input>
            <Input type="textarea" name="bio" {...{onChange}}>
                Info
            </Input>
            <Button type="submit">
                Submit
            </Button>
        </form>
    )
}