import React from "react"
import { Routes, Route, Link } from "react-router-dom"
import useGraphql from "@hooks/use-graphql"
import { User as UserType } from "@graphql/users"
import User from "../user"


export default function Users() {
    const { data, loaded } = useGraphql<UserType[]>({
        query: `{
            getAllUsers {
                username
                email
            }
        }`
    })

    const RenderList = React.useCallback(() => {
        return data.map((item, index) => {
            const entries: JSX.Element[] = []

            for (const [key, value] of Object.entries(item)) {
                entries.push(
                    <div key={value.toString()}>
                        <span>
                            {key}: {value}
                        </span>
                    </div>
                )
            }
            
            return (
                <div key={index} style={{border: '1px solid', display: 'inline-block'}}>
                    {entries}
                    <div>
                        <Link to={encodeURIComponent((item.email))}>
                            View User
                        </Link>
                    </div>
                </div>
            )
        })
    }, [data])

    React.useEffect(() => {
        document.title = 'Users'
    }, [])

    return(
        <>
            <div>
                {loaded ? RenderList() : 'Loading...'}
            </div>
            <Routes>
                <Route path=":email" element={<User/>}/>
            </Routes>
        </>
    )
}